import '../styles/About.scss'

function About({identifier, classname="boxAbout", classNameTitle="subTitle", classNameParagraph="boxAbout__paragraph"}) {
    return (
        <section id={identifier} className={classname}>
            <h2 className={classNameTitle}> Propos de moi</h2>
            <p className={classNameParagraph}>Aprs une dizaine d'annes dans un tout autre domaine, je me suis form dans le dveloppement web  travers la formation Dveloppeur Web avec OpenClassrooms. Cet un domaine dans lequel je m'tais lanc plus jeune et que j'ai pratiqu comme un passe-temps. <br/><br/>Le front-end me passionne autant que le back-end. Ce qui me plat tout particulirement est la rflexion qu'il faut avoir avant de se lancer dans le code et la rflexion qu'il faut avoir pour trouver des solutions aux problmes rencontrs lors du dveloppement.<br/><br/> Pour moi le dveloppement est un peu comme le bricolage au sens noble du terme,  savoir que l'on peut ralis tout ce que l'on souhaite, la seule limite tant notre imagination. Et autre point similaire, les meilleurs outils ne garantissent pas une grande ralisation si le travail de rflexion est insuffisant.</p>
        </section>
    )
}

export default About;